import { isSocialWall, isTagEmbed, isEmbed, isSocialEmbed } from "../../../../../actions/api";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../../Elements/PostContent/PostContent'
import CTAButton from "../../../Elements/CTA/CtaButton";
import Rating from "../../../Elements/Rating/Rating";
import ShareAndSocialActions from "./ShareAndSocialActions";
import { IS_THEME, PostTrimContent } from '../../../../../utils'
import { useState } from "react";
import { InView } from 'react-intersection-observer';


const Card = ({ postVisible, itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall }) => {
  const customTrimUser = wall?.Wall?.owner == 175970 ? true : false
  const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
  const rating = (itemData.rating > 0) ? true : false;
  const contentClass = (itemData.rating > 0) ? `tb_w_c_rating_content` : `tb_w_c_content ${personalization.trimcontent && customTrimUser ? `tb_content_line-3 word_white_space` : ``}`
  const tagEmbedReviewContentClass = isTagEmbed && IS_THEME(5) ? 'tb_w_c_rating_content_review' : null
  const textDecorate = (itemData.type === 1 && personalization.textDecorate && String(itemData.content).length > 0) ? 'tb_w_c_text_decoration tb_w_c_text_post' : '';
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
  const cardActions = (ctaStatus || ThemeRule.socialAction || ThemeRule.shareOption) && !isSocialWall ? true : false;
  const trim_content_length = customTrimUser ? 200 : itemData.type === 1 ? 50 : 50
  const postTextContent = personalization.trimcontent ? PostTrimContent(customTrimUser ? String(itemData.content).trim() : itemData.content, trim_content_length) : itemData.content
  const outerpadding = isSocialWall ? { padding: ThemeRule.spacing } : null

  return (

    <div id={`tb-cc-post-${itemData.id}`} className={`tb_w_c_post_wrapper`} style={{
      width: `${adjustWidth}%`, padding: personalization.padding / 2
    }} tb-network={itemData.network.icon}>
    
   <InView
    threshold={0.9} // Trigger when 10% of the element is visible
    triggerOnce={true} // Load the image only once
  >
    {({ inView, ref }) => (
      <div ref={ref} style={{ minHeight: '400px', width: '100%' }}>
        {inView ? (
      
      <div className="tb_w_c_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} style={outerpadding}>
 
        {isTagEmbed || IS_THEME(62) || IS_THEME(78) || IS_THEME(66) ? (mediaType ?
          <div className="tb_w_c_post_media_wrapp">
            <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} />
          </div>
          : '') : null}


        {(!ThemeRule.hideContent || personalization.postAuthor) ? <div className="tb_w_c_contant_wrapper">
          <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
          {rating ? <div className="tb_w_c_rating__"><Rating rating={itemData.rating} network={itemData.network} /> </div> : ''}
          {((!ThemeRule.hideContent || itemData.type == 1) && !IS_THEME(63)) ? <PostContent item={itemData} contentClass={`${contentClass} ${tagEmbedReviewContentClass} ${textDecorate} `} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
        </div> : null}

        {(!isTagEmbed && !IS_THEME(62) && !IS_THEME(78) && !IS_THEME(66)) ? (mediaType ?
          <div className="tb_w_c_post_media_wrapp">
            <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} />
          </div>
          : '') : null}

        {cardActions || ctaStatus || ThemeRule.shareOption ?
          <div className="tb_w_c_post_actions">
            {ctaStatus ? <div className="tb_w_c_post_cta"><CTAButton ctaClass={'tb_w_c_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
            {cardActions ? <ShareAndSocialActions itemData={itemData} ThemeRule={ThemeRule} /> : ''}
          </div>
          : ''}
    
    </div>
     ) : (
      <div
        decoding="async"
        style={{
          backgroundColor: 'red',
          width: '100%',
        }}
      ></div>
    )}
  </div>
)}
</InView>
</div>

  );
}
export default Card