import React, { Component } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../../actions/themeActions';
import ScrollAnimation from "../../../Components/ScrollAnimation";
import "./scss/Casecade.scss";
class ClassicCard extends Component {
  state = {
    postData: [],
    postVisible : false
  }

  componentDidMount() {
    const { postData } = this.props;
    const self = this;
    this.onLoadMasonry()


    window.addEventListener('wheel', function () {
      self.scrollList()
    }, true);


    window.addEventListener('resize', function (event) {
      self.editorHeight();
    }, true);
  }

  componentWillUnmount() {
    const self = this;
    window.removeEventListener('resize', function (event) {
      self.editorHeight();
    }, true);

   window.removeEventListener('wheel',self.scrollList() )

  }

  scrollList=()=> this.setState({ postVisible : true}) 
   
  onLoadMasonry = () => {
    this.editorHeight();
    const { heightUpdate } = this.props;
    setTimeout(() => heightUpdate(), 1000)
  }


  editorHeight = () => {
    var span = document.querySelector(".tb_w_c_post_container");

    if (span) {
      const getHeight = span.style.height;
      if (getHeight) {
        const height = getHeight.split("px");
        if (height && JSON.stringify(height) && height[0] > 0) {
          this.props.managePostHeight(`${getHeight}`);
        }
      }
    }
  }

  // componentWillReceiveProps(nextProps) {
  //   const { postData } = nextProps;
  //   if (this.props.postData != nextProps.postData) this.onLoadMasonry()
  // }
  // componentDidUpdate(prevProps, prevState) {
  //   if (prevProps.postData !== this.props.postData) return true
  //   else return false
  // }



  render() {
    const { postData, completeDataObject, adjustWidth, wall, languageSetting, clickToShowPopUp, onClickToCTA } = this.props;

    return <ScrollAnimation wall={wall} parentId="tb_marquee" renderID="tb_cascade" >
      <div id="tb_marquee">
        <Masonry
          id={`tb_cascade`}
          className={`tb_w_c_post_container`}
          tabIndex='0'
          aria-label={`There are ${postData?.length || 0} posts in the feed`}
          elementType={'div'}
          options={{
            transitionDuration: 0,
            percentPosition: true,
            itemSelector: '.tb_w_c_post_wrapper',
            horizontalOrder: false,
            resize: true,
          }}
          disableImagesLoaded={false}
          updateOnEachImageLoad={false}
          imagesLoadedOptions={{}}
          enableResizableChildren={true}>
          {

            ((postData?.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return <Card ownerId={wall.Wall.owner} postVisible={this.state.postVisible} itemData={cardData} key={`cls_crd_${index}_${cardData.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} completeDataObject={completeDataObject} postData={postData} languageSetting={languageSetting} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} wall={wall} />
            })
          }
        </Masonry>
      </div>
    </ScrollAnimation>
  }
}

const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClassicCard)
